/* eslint-disable no-unused-vars, no-param-reassign */

import { PURGE } from "redux-persist";
import { createSlice } from "@reduxjs/toolkit";

import { ActiveUserResponse, UserPlayPreferenceResponse } from "../../../src/types/types";

export type ActiveUserState = {
  activeUser: ActiveUserResponse | undefined,
};

export type ActiveUserStateReducers = {
  setActiveUser: (state: ActiveUserState, action: {
    type: string,
    payload: { user: ActiveUserResponse | undefined; },
  }) => void;
  upsertActiveUserPlayPreference: (state: ActiveUserState, action: {
    type: string,
    payload: { preference: UserPlayPreferenceResponse; },
  }) => void;
  deleteActiveUserPlayPreference: (state: ActiveUserState, action: {
    type: string,
    payload: { id: string; },
  }) => void;
};

export const activeUserSlice = createSlice<ActiveUserState, ActiveUserStateReducers>({
  name: "activeUser",
  initialState: {
    activeUser: undefined,
  },
  reducers: {
    setActiveUser: (state, action: {
      type: string,
      payload: { user: ActiveUserResponse | undefined; },
    }) => {
      state.activeUser = action.payload.user;
    },
    upsertActiveUserPlayPreference: (state, action: {
      type: string,
      payload: { preference: UserPlayPreferenceResponse; },
    }) => {
      if (state.activeUser) {
        const currentPreferenceIndex = state.activeUser.playPreferences?.findIndex((p) => p.id === action.payload.preference.id);

        let newPreferences = state.activeUser.playPreferences || [];

        if (!(currentPreferenceIndex < 0)) {
          newPreferences[currentPreferenceIndex] = action.payload.preference;
        } else {
          newPreferences = newPreferences.concat(action.payload.preference);
        }

        state.activeUser.playPreferences = newPreferences;
      }
    },
    deleteActiveUserPlayPreference: (state, action: {
      type: string,
      payload: { id: string; },
    }) => {
      if (state.activeUser) {
        const currentPreferenceIndex = state.activeUser.playPreferences?.findIndex((p) => p.id === action.payload.id);

        if (!(currentPreferenceIndex < 0)) {
          state.activeUser.playPreferences.splice(currentPreferenceIndex, 1);
        }
      }
    },
  },
  extraReducers: (builder) => builder.addCase(PURGE, (state) => {
    state.activeUser = undefined;
  }),
});

export const { setActiveUser, upsertActiveUserPlayPreference, deleteActiveUserPlayPreference } = activeUserSlice.actions;
export const selectActiveUser = (state: { activeUser: ActiveUserState; }) => state.activeUser.activeUser;

export default activeUserSlice.reducer;
